//     
let tasks = {
    active: [],     //  
    completed: []   //  
};

//    
let categories = {};

//      
let activeEditors = {};

// ,        
let pieChart = null;

//       
const CHART_TYPES = {
    COMPLETION: 'completion',   //  /                       
    COMPLETED_BY_CATEGORY: 'completed-by-category',  //            
    ACTIVE_BY_CATEGORY: 'active-by-category'    //      
};

// ,     (  "/")
let currentChartType = CHART_TYPES.COMPLETION;

// ,        
let productivityChart = null;

//       
const PRODUCTIVITY_PERIODS = {
    LAST_7_DAYS: '7',
    LAST_14_DAYS: '14',
    LAST_30_DAYS: '30',
    CUSTOM: 'custom'
};

// ,      
const defaultCategoryId = 'other';

// ,    
let currentCategoryFilter = 'all';  //    
let currentStatusFilter = 'all';    //     (/)

//      
const EXPORT_TYPES = {
    SELECTED: 'selected',
    ALL_TASKS: 'all-tasks',
    ALL_COMPLETED: 'all-completed',
    ALL_ACTIVE: 'all-active',
    BY_DATE_RANGE: 'by-date-range'
};

