const API_BASE_URL = window.location.origin + '/api';

//     API
class TodoAPI {
    constructor() {
        this.token = localStorage.getItem('auth_token');
        
        //     
        window.addEventListener('storage', (e) => {
            if (e.key === 'auth_token') {
                this.token = e.newValue;
            }
        });
    }

    //    
    setToken(token) {
        this.token = token;
        localStorage.setItem('auth_token', token);
    }

    //     
    async fetchAPI(endpoint, options = {}) {
        if (!this.token) {
            window.location.href = '/login.html';
            throw new Error(' ');
        }

        const url = `${API_BASE_URL}${endpoint}`;
        const headers = {
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${this.token}`,
            ...options.headers
        };

        try {
            const response = await fetch(url, {
                ...options,
                headers
            });

            if (!response.ok) {
                if (response.status === 401) {
                    //   ,    
                    window.location.href = '/login.html';
                    throw new Error(' ');
                }
                const error = await response.json();
                throw new Error(error.error || 'API Error');
            }

            return await response.json();
        } catch (error) {
            console.error('API Error:', error);
            throw error;
        }
    }

    //     
    async getCategories() {
        return this.fetchAPI('/categories');
    }

    async createDefaultCategories() {
        return this.fetchAPI('/categories/default', {
            method: 'POST'
        });
    }

    async createCategory(name, color) {
        return this.fetchAPI('/categories', {
            method: 'POST',
            body: JSON.stringify({ name, color })
        });
    }

    async updateCategory(categoryId, data) {
        return this.fetchAPI(`/categories/${categoryId}`, {
            method: 'PUT',
            body: JSON.stringify(data)
        });
    }

    async deleteCategory(categoryId) {
        return this.fetchAPI(`/categories/${categoryId}`, {
            method: 'DELETE'
        });
    }

    //     
    async getTasks() {
        return this.fetchAPI('/tasks');
    }

    async findTask(taskId) {
        return this.fetchAPI(`/tasks/${taskId}`);
    }

    async createTask(taskData) {
        return this.fetchAPI('/tasks', {
            method: 'POST',
            body: JSON.stringify(taskData)
        });
    }

    async updateTask(taskId, taskData) {
        return this.fetchAPI(`/tasks/${taskId}`, {
            method: 'PUT',
            body: JSON.stringify(taskData)
        });
    }

    async deleteTask(taskId) {
        return this.fetchAPI(`/tasks/${taskId}`, {
            method: 'DELETE'
        });
    }

    //    
    async completeTask(taskId) {
        return this.fetchAPI(`/tasks/${taskId}/status`, {
            method: 'PATCH',
            body: JSON.stringify({ status: 'completed' })
        });
    }

    //    
    async uncompleteTask(taskId) {
        return this.fetchAPI(`/tasks/${taskId}/status`, {
            method: 'PATCH',
            body: JSON.stringify({ status: 'active' })
        });
    }

    /**
     *   
     * @param {Array<{taskId: string, order: number}>} orderData -     
     * @returns {Promise<void>}
     */
    async updateTaskOrder(orderData) {
        return this.fetchAPI('/tasks/order', {
            method: 'PATCH',
            body: JSON.stringify({ orderData })
        });
    }

    /**
     *      
     * @param {Object} params -  
     * @param {string} params.startDate -  
     * @param {string} params.endDate -  
     * @returns {Promise<Array>}     
     */
    async getProductivityData(params) {
        const queryParams = new URLSearchParams(params);
        return this.fetchAPI(`/tasks/stats/productivity?${queryParams.toString()}`);
    }

    /**
     *    
     * @param {string} taskId - ID 
     * @param {Object} notificationSettings -  
     * @param {boolean} notificationSettings.notifications_enabled -   
     * @param {string|null} notificationSettings.notification_time -    ISO 
     * @returns {Promise<Object>}  
     */
    async updateTaskNotifications(taskId, notificationSettings) {
        return this.fetchAPI(`/tasks/${taskId}/notifications`, {
            method: 'PATCH',
            body: JSON.stringify(notificationSettings)
        });
    }
}

//     API
const todoAPI = new TodoAPI();
window.todoAPI = todoAPI; //   
export default todoAPI; 